/* General styling */
body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #f4f4f9;
  color: #333;
}

.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* Header styling */
.header {
  background-color: #6200ea;
  color: #fff;
  text-align: center;
  padding: 1rem;
}

/* Main content styling */
.main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1rem;
}

.webcam {
  width: 100%;
  max-width: 400px;
  border: 2px solid #ccc;
  border-radius: 8px;
}

.image-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.captured-image {
  width: 100%;
  max-width: 400px;
  border: 2px solid #6200ea;
  border-radius: 8px;
  margin-bottom: 1rem;
}

.button-group {
  display: flex;
  gap: 1rem;
}

.capture-btn {
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  color: #fff;
  background-color: #6200ea;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.capture-btn:hover {
  background-color: #3700b3;
}

.scan-btn {
  background-color: #03a9f4;
}

.scan-btn:hover {
  background-color: #0288d1;
}

.ocr-result {
  text-align: center;
  max-width: 400px;
  background-color: #fff;
  border: 2px solid #6200ea;
  border-radius: 8px;
  padding: 1rem;
}

.ocr-result h2 {
  color: #6200ea;
}

.ocr-result p {
  word-wrap: break-word;
}

/* Footer styling */
.footer {
  background-color: #6200ea;
  color: #fff;
  text-align: center;
  padding: 1rem;
}
